export default function Follow({ data }) {
  return (
    <div className="follow">
      <h2>Follow me here:</h2>
      <ul className="list">
        {data &&
          data.map((link) => (
            <li key={link.URL}>
              <a href={link?.URL} target="_blank" rel="noopener noreferrer">
                {link?.name}
              </a>
            </li>
          ))}
      </ul>
      <p className="download__link" title="resume">
        <a href="/Pawan_FE.pdf" rel="noopener noreferrer" download>
          <svg
            className="w-5 h-5"
            fill="none"
            stroke="currentColor"
            viewBox="0 0 24 24"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M9 19l3 3m0 0l3-3m-3 3V10"
            />
          </svg>
        </a>
      </p>
    </div>
  );
}
